body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

* {
  line-height: 1.7;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 19px;
}

h4 {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: .5rem;
  font-family: inherit;
  font-weight: 500;
  line-height: 1.2;
  color: inherit;
}

.cont-banner-text {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  padding-left: 5px;
  padding-right: 5px;
  word-wrap: break-word;
}

.content-left {
  width: calc(41.66666666666667% - 10px);
}

.content-right {
  width: calc(58.333333333333336% - 10px);
}

.cont-text {
  height: 50%;
  align-items: center;
  padding: 0 20px;
}

.cont-text p {
  padding-top: 20px;
}

.cont-btn {
  display: flex;
  text-align: center;
  background-color: #e0e3e5;
  height: 48.8%;
}

.cont-btn div {
  margin: auto;
}

.cont-btn div h3 {
  font-size: 50px;
  color: #e20101;
  font-weight: 800;
}

.cont-btn div button {
  background-color: #e20101;
  min-width: 350px;
  min-height: 50px;
  font-size: 40px;
  border-radius: 15px;
  color: #ffffff;
}

.princep {
  background-color: #48525a;
  color: #ffffff;
  padding-top: 20px;
}

.princep div {
  padding-right: 20px;
}

.princep p {
  padding-bottom: 20px;
  text-align: right;
}

.time {
  display: flex;
  color: #ffffff;
  min-height: 400px;
  background-image: url(../img/time.jpg);
  line-height: 1.5;
}

.time p {
  padding: 20px;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .content-left {
    width: calc(33.333333333333336% - 10px);
  }

  .content-right {
    width: calc(66.66666666666667% - 10px);
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .content-left {
    width: calc(100% - 10px);
  }

  .content-right {
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}